import * as React from "react"
import { IoStar } from "react-icons/io5"

const CircleBorder = () => (
    <div
        style={{
            width: 23,
            height: 23,
            borderRadius: 150,
            display: "flex",
            justifyContent: "center",
            alignItems: "center",
            borderColor: "black",
            borderWidth: 1,
            backgroundColor: "#343a40",
            marginRight: 5,
        }}
    ></div>
)

function Rating(props) {
    const ratingList = []
    for (let i = 0; i < props.rating; i++) {
        ratingList.push(<IoStar key={"rating" + i} />)
    }

    return (
        <div
            style={{
                display: "flex",
                flexDirection: "row",
            }}
        >
            {ratingList.map((_, index) => {
                return <CircleBorder key={"circleBorder" + index} />
            })}
        </div>
    )
}

export default function SkillItem(props) {
    return (
        <div
            style={{
                display: "flex",
                flex: 1,
                width: "100%",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "space-between",
            }}
        >
            <p style={{ marginRight: 15 }}>{props.text}</p>
            <Rating rating={props.rating} />
        </div>
    )
}
