import * as React from "react"

export default function HighlightWrapper(props) {
    const [mouseOver, setMouseOver] = React.useState(null)
    React.useEffect(() => {
        // console.log("MouseOver: " + mouseOver)
    }, [mouseOver])
    function MouseOver(index) {
        mouseOver === null && setMouseOver(index)
    }
    function MouseOut() {
        mouseOver !== null && setMouseOver(null)
    }

    return (
        <div>
            {React.Children.map(props.children, (child, index) => (
                <div
                    style={{
                        cursor: "pointer",
                        backgroundColor:
                            mouseOver === index ? "#e2eafc" : "transparent",
                        padding: 8,
                        borderRadius: 5,
                        transition: "all .5s ease",
                        WebkitTransition: "all .5s ease",
                        MozTransition: "all .5s ease",
                        marginBottom: props.marginBottom
                            ? props.marginBottom
                            : 0,
                    }}
                    onMouseOver={() => {
                        MouseOver(index)
                    }}
                    onMouseOut={MouseOut}
                >
                    {child}
                </div>
            ))}
        </div>
    )
}
