import * as React from "react"
import { CopyToClipboard } from "react-copy-to-clipboard"
import { toast } from "react-toastify"

function getTypeToastMessage(type) {
    switch (type) {
        case "email":
            return "Email address copied"
        case "phone":
            return "Phone number copied"
        default:
            return "Copied"
    }
}

export default function ContactItem(props) {
    // const [size, setSize] = React.useState([0, 0])
    // React.useEffect(() => {
    //     // console.log("Window size: " + size[0])
    // }, [size])

    // React.useLayoutEffect(() => {
    //     function updateSize() {
    //         setSize([window.innerWidth, window.innerHeight])
    //     }
    //     window.addEventListener("resize", updateSize)
    //     return () => window.removeEventListener("resize", updateSize)
    // }, [])

    const notify = () =>
        toast(getTypeToastMessage(props.type), {
            position: "top-center",
            autoClose: 2000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
        })

    return (
        <div
            style={{
                display: "flex",
                flex: 1,
                flexDirection: "row",
                justifyContent: "flex-end",
            }}
        >
            <CopyToClipboard
                onCopy={notify}
                text={props.text.replace(/ /g, "")}
            >
                <span style={{ marginRight: 15 }}>{props.text}</span>
            </CopyToClipboard>
            <props.icon />
        </div>
    )
}
