import { createMaterialTopTabNavigator } from "@react-navigation/material-top-tabs"
import { NavigationContainer } from "@react-navigation/native"

const Tab = createMaterialTopTabNavigator()

const tabFontSize = 14

const Null = () => <></>

export default function TopBar(props) {
    return (
        <div style={{ width: "100vw" }}>
            <NavigationContainer>
                <Tab.Navigator
                    screenOptions={{
                        tabBarIndicatorStyle: {
                            backgroundColor: null,
                        },
                    }}
                >
                    <Tab.Screen
                        name="Skills"
                        component={Null}
                        listeners={{
                            tabPress: (e) => {
                                props.refs.skillRef.current.scrollIntoView({
                                    behavior: "smooth",
                                })
                            },
                        }}
                        options={{
                            tabBarLabel: ({ focused, color }) => (
                                <p
                                    style={{
                                        color: "#495057",
                                        fontSize: tabFontSize,
                                    }}
                                >
                                    Skills
                                </p>
                            ),
                        }}
                    />
                    <Tab.Screen
                        name="Socials"
                        component={Null}
                        listeners={{
                            tabPress: (e) => {
                                props.refs.socialRef.current.scrollIntoView({
                                    behavior: "smooth",
                                })
                            },
                        }}
                        options={{
                            tabBarLabel: ({ focused, color }) => (
                                <p
                                    style={{
                                        color: "#495057",
                                        fontSize: tabFontSize,
                                    }}
                                >
                                    Socials
                                </p>
                            ),
                        }}
                    />
                    <Tab.Screen
                        name="Languages"
                        component={Null}
                        listeners={{
                            tabPress: (e) => {
                                props.refs.languageRef.current.scrollIntoView({
                                    behavior: "smooth",
                                })
                            },
                        }}
                        options={{
                            tabBarLabel: ({ focused, color }) => (
                                <p
                                    style={{
                                        color: "#495057",
                                        fontSize: tabFontSize,
                                    }}
                                >
                                    Languages
                                </p>
                            ),
                        }}
                    />
                </Tab.Navigator>
            </NavigationContainer>
        </div>
    )
}
